    border-bottom: 1px solid #ddd;
    margin-bottom: 50px;
}
.outer_box .text_box .text {
    float: left;
}
.outer_box .text_box .rostery_count {
    text-align: end;
    font-size: 15px;
    line-height: 30px;
}

.Rostery_list_container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 100px;
}
.rostery_box {
    width: 300px;
    height: 350px;
    background-color: #FAF7E8;
    padding: 50px;
    box-sizing: border-box;
    border-radius: 30px;
    margin-bottom: 30px;
    text-align: center;
    transition: 0.3s;
}
.rostery_box:hover {
    border-radius: 0;
    border: 3px solid #DEA462;
    box-sizing: border-box;
}

.rostery_box .img_box {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto;
    
}
.rostery_box .img_box img {
    width: 100%;
}

.rostery_box .text {
    font-size: 20px;
    margin-top: 40px;
    background-color: white;
    transition: 0.3s;
}

.rostery_box:hover .text {
    background-color: #DEA462;
}
